/* Custom Styles */
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap');
$font-stack: Helvetica, sans-serif;
$primary-color: rgb(0, 0, 0);

body {
  color: $primary-color;
  max-width: 1080px; 
  border-style: solid;
	margin: 0 auto !important; 
	float: none !important; 
}

h1{
  font-family: 'Amatic SC', cursive;
}


/* Sizing for Project Containers  */

.column-4 {
  
  width: 190px;
  padding: 10px;
  margin:10%;
 // margin-left:10%;
  height: 250px;
  display:inline-block;
  font-size: 30px;
  border-radius: 25px;
}

.project-1 {
  background: url('../images/onegin.jpg');
  background-size: cover;
}

.project-2 {
  background: url('../images/glenfinnan.jpg');
  background-size: cover;
  text-align: center;
}

.project-3 {
  background: url('../images/fulghum.jpg');
  background-size: cover;
  text-align: center;
}

.project-4 {
  background: url('../images/soukupova.jpg');
  background-size: cover;
  text-align: center;
  border:1px solid black;
}

.project-5 {
  background: url('../images/Zemepisec.png');
  background-size: cover;
  text-align: center;
}

.tiles{
  text-align:center;
  display: grid;
  justify-content: center;
  grid-auto-flow: column;
  gap: 4px;
  margin-bottom: 50%;
}

.uvod{
  margin-left: 10%;
  margin-bottom: 20%;
}

